import React from "react";
import { isValidPhoneNumber } from "react-phone-number-input";
import PhoneField from "./PhoneField";
import PasswordField from "./PasswordField";
import styles from "./Form.module.css";

const INIT_VALUES = {
  phone: "",
  password: "",
  privacyPolicy: false,
  agree: false,
};
const INIT_ERRORS = {
  phone: "",
  password: "",
  privacyPolicy: "",
  agree: "",
};
const MIN_PASSWORD_LENGTH = 6;

const Form = () => {
  const [values, setValues] = React.useState(INIT_VALUES);
  const [errors, setErrors] = React.useState(INIT_ERRORS);

  const handleInput = (name) => (e) => {
    setValues((prev) => ({
      ...prev,
      [name]: name === "phone" ? e : e.target.value,
    }));
  };

  const handleCheckbox = (name) => (e) => {
    setValues((prev) => ({ ...prev, [name]: e.target.checked }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const newErrors = { phone: "", password: "", privacyPolicy: "", agree: "" };
    const { phone, password, privacyPolicy, agree } = values;

    if (!phone) {
      newErrors.phone = "  ";
    } else if (!isValidPhoneNumber(phone)) {
      newErrors.phone = "   ";
    }
    if (!password) {
      newErrors.password = "  ";
    } else if (password.length < MIN_PASSWORD_LENGTH) {
      newErrors.password = "    6 ";
    }
    if (!privacyPolicy) {
      newErrors.privacyPolicy = "  ";
    }
    if (!agree) {
      newErrors.agree = "  ";
    }

    if (Object.values(newErrors).some((error) => !!error)) {
      setErrors(newErrors);
    } else {
      setValues(INIT_VALUES);
      setErrors(INIT_ERRORS);
      alert("");
    }
  };

  return (
    <form className={styles.container} onSubmit={handleSubmit}>
      <div className={styles.main}>
        <p className={styles.title}></p>
        <div className={styles.inputList}>
          <PhoneField
            value={values.phone}
            onChange={handleInput("phone")}
            error={errors.phone}
          />
          <PasswordField
            value={values.password}
            onChange={handleInput("password")}
            error={errors.password}
          />
        </div>
        <div className={styles.checkboxList}>
          <div className={styles.checkboxItem}>
            <input
              type="checkbox"
              id="check1"
              checked={values.privacyPolicy}
              name="privacyPolicy"
              onChange={handleCheckbox("privacyPolicy")}
            />
            <label htmlFor="check1">
              <p>  21 .</p>
              <p>
                   {" "}
                <a href="/link">"  "</a> {" "}
                <a href="/link">" "</a>
              </p>
            </label>
            {!!errors.privacyPolicy && (
              <p className={styles.errorMessage}>{errors.privacyPolicy}</p>
            )}
          </div>
          <div className={styles.checkboxItem}>
            <input
              type="checkbox"
              id="check2"
              checked={values.agree}
              name="agree"
              onChange={handleCheckbox("agree")}
            />
            <label htmlFor="check2">
              <p>
                     {" "}
                <a href="/link"> </a>
              </p>
            </label>
            {!!errors.agree && (
              <p className={styles.errorMessage}>{errors.agree}</p>
            )}
          </div>
        </div>
        <button type="submit" className={styles.button}>
          
        </button>
      </div>
    </form>
  );
};

export default Form;
